.login-container {
  background-image: linear-gradient(to left, rgba(#000, 0.6), rgba(#000, 0.6)),
    url("../images/background.jpg");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: right;
  width: 100vw;
  height: 100vh;
  overflow: hidden;

  form {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-image: linear-gradient(
        to left,
        rgba(223, 68, 18, 0.7) 0%,
        rgba(221, 66, 19, 0.7) 7%,
        rgba(223, 68, 18, 0.7) 23%,
        rgba(171, 9, 47, 0.7) 100%
      ),
      url("../images/form.png"),
      linear-gradient(
        to left,
        rgba(223, 68, 18, 1) 0%,
        rgba(221, 66, 19, 1) 7%,
        rgba(223, 68, 18, 1) 23%,
        rgba(171, 9, 47, 1) 100%
      );
    background-size: contain;
    background-repeat: no-repeat;
    background-position: bottom;
    .logo-box {
      margin-bottom: 70px;
    }
    .form-box {
      .d-flex {
        margin-top: 20px;
        justify-content: space-between;
        label {
          color: #fff;
          margin-left: 7px;
        }
        a {
          color: #fff;
        }
      }
    }
  }
  .left {
    height: 100vh;
    display: flex;
    justify-content: center;
    padding: 10px 40px;
    flex-direction: column;
    @media (max-width: 768px) {
      padding: 5px 20px;
    }
    h2 {
      color: #fff;
      margin-top: 60px;
      font-size: 40px;
      width: 70%;
      margin-bottom: 20px;
    }
    p {
      color: #fff;
    }
  }
}
.fron-input {
  width: max-content;
  margin-bottom: 24px;
  input {
    background-color: transparent;
    width: 300px;
    border: none;
    outline: none;
    transition: 0.5s;
    border-bottom: 1px solid #ffffff;
    color: #ffffff;
    padding: 10px 25px;
    &::placeholder {
      color: #ffffff;
    }
    &:focus {
      outline: none;
      border-color: rgba(221, 66, 19, 1);
    }
  }
  position: relative;
  i {
    position: absolute;
    left: 4px;
    top: 50%;
    transform: translateY(-50%);
    color: #ffffff;
  }
}
