.side-nav-conatiner {
  background: linear-gradient(
    to bottom,
    rgba(171, 9, 47, 1) 0%,
    rgba(223, 68, 18, 1) 100%,
    rgba(223, 68, 18, 1) 100%
  );
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 240px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: 0.4s;
  z-index: 200;
  &.active {
    left: -240px;
  }
  .img-box {
    margin-bottom: auto;
    padding: 15px;
  }
  ul {
    width: 100%;
    height: 100%;
    margin-top: 17px;
    li {
      position: relative;
      &::before {
        content: "";
        position: absolute;
        width: 7px;
        height: 70%;
        background-color: $color2;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        opacity: 0;
        transition: 0.5s;
        border-top-right-radius: 20px;
        border-bottom-right-radius: 20px;
      }
      a {
        display: flex;
        align-items: center;
        transition: 0.5s;
        text-decoration: none;
        padding: 15px 16px;
        img {
          // width: 27px;
          display: block;
          // height: 27px;
          margin-right: 20px;
          transition: 0.5s;
        }
        h3 {
          color: #fff;
          font-size: 24px;
          margin-bottom: 0;
        }
      }
      &:hover,
      &.active {
        a {
          background-color: rgba(#000, 0.3);
          img {
            filter: invert(34%) sepia(79%) saturate(4021%) hue-rotate(358deg)
              brightness(92%) contrast(89%);
          }
          h3 {
            color: $color2;
          }
        }
        &::before {
          opacity: 1;
        }
      }
    }
  }
}
nav {
  padding: 10px;
  width: 100%;
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  .sideNavButton {
    background-color: transparent;
    font-size: 27px;
    color: rgb(174, 174, 174);
    cursor: pointer;
    border: none;
    outline: none;
    transition: 0.5s;
    &:hover {
      color: $color1;
    }
  }
  ul {
    display: flex;
    padding: 0 10px;
    align-items: center;
    li {
      margin-right: 5px;
      position: relative;
      .num {
        width: 15px;
        height: 15px;
        border-radius: 50%;
        background-color: #8c60b5;
        &.envelope {
          background-color: #d83c16;
        }
        border: 1px solid #fff;
        position: absolute;
        right: 0;
        font-size: 10px;
        display: flex;
        align-items: center;
        justify-content: center;
        top: -4px;
        color: #fff;
      }
      button {
        img {
          width: 30px;
          height: 30px;
          margin-bottom: 6px;
        }
        i {
          color: #b0b0b0;
          font-size: 20px;
          transition: 0.5s;
          &:hover {
            color: $color1;
          }
        }
      }
    }
  }
}
