ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
.main-buttin {
  padding: 10px 14px;
  border-radius: 100px;
  width: 100%;
  display: block;
  background: linear-gradient(
    352deg,
    rgba(223, 68, 18, 1) 0%,
    rgba(221, 66, 19, 1) 7%,
    rgba(223, 68, 18, 1) 23%,
    rgba(171, 9, 47, 1) 100%
  );
  color: #fff;
  text-decoration: none;
  transition: 0.5s;
  background-size: 200%;
  &:hover {
    color: #fff;
    text-decoration: none;
    background-size: 10%;
  }
}
@import "import";

