* {
  font-family: "MYRIADPRO-REGULAR";
}
a {
  text-decoration: none;
}
button {
  background-color: transparent;
  cursor: pointer;
  border: none;
  outline: none;
  &:visited,
  &:active,
  &:focus {
    outline: none;
  }
}
select {
  &:focus,
  &:active,
  &:visited {
    outline: none;
  }
}
body {
  &::-webkit-scrollbar {
    background-color: transparent;
    width: 7px;
  }
  &::-webkit-scrollbar-thumb {
    background-color: $color1;
    border-radius: 100px;
  }

  background-color: rgb(234, 238, 241);
  .min-content-container {
    padding-left: 240px;
    transition: 0.4s;
    &.active {
      padding-left: unset;
    }
    .topHeader {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 20px;
      width: 100%;
      @media (max-width: 480px) {
        flex-direction: column;
      }
      .title {
        @media (max-width: 480px) {
          text-align: center;
        }
      }
      .controls {
        display: flex;
        align-items: center;
        @media (max-width: 480px) {
          flex-direction: column;
        }

        .input-group-prepend {
          margin-right: 20px;
          width: 200px;
          padding: 0 5px;
          border-radius: 100px;
          background-color: #fff;
          @media (max-width: 480px) {
            width: 250px;
            margin-right: unset;
            margin-bottom: 16px;
          }

          .custom-select {
            width: 100%;
            background-color: transparent;
            border: none;
          }
        }
        .input-box {
          width: 250px;
          padding: 3px 10px;
          border-radius: 100px;
          background-color: #fff;
          position: relative;
          input {
            width: 100%;
            padding: 3px 19px;
            border: none;
            outline: none;
          }
          button {
            position: absolute;
            right: 5px;
            top: 50%;
            transform: translateY(-50%);
            i {
              color: rgb(174, 174, 174);
            }
          }
        }
      }
    }
  }
}
.client-box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  border-radius: 6px;
  background-color: rgb(255, 255, 255);
  box-shadow: 0px 15px 30px 0px rgba(195, 208, 217, 0.3);
  margin-bottom: 20px;
  padding: 30px 10px;
  .image-box {
    width: 240px;
    margin-bottom: 20px;
  }
  h2 {
    font-size: 20px;
  }
  p {
    font-size: 16px;
    color: rgb(138, 138, 138);
  }
}
